






















































































































import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { LoadingScreen } from "@/mixins/loadingScreen";
import NumberCard from "@/components/home/NumberCard.vue";
import TheLoginBox from "@/components/misc/TheLoginBox.vue";
import {
  ILoginArgs,
  ICreateUserArgs,
  IClient,
  IAdminClient,
  ICreateBackupRepositoryArgs,
  ICreateBackupRepositoryOnClientArgs,
  ICreateBackupJobArgs,
  IRepositoryOptionalId,
  Overwrite,
  RepoType
} from "@/shared";
import NewJobView from "./NewJob.vue";
import RepositoryEditor from "@/components/misc/RepositoryEditor.vue";
import JobEditor, { JobOptionalId } from "@/components/misc/JobEditor.vue";
import HomeWizard from "@/components/home/HomeWizard.vue";
import { IJob } from "@/store/modules/JobsModule";
import { IRepositoryWithClientId } from "@/store/modules/AdminModule";
import {
  routerModule,
  jobsModule,
  repositoriesModule,
  adminModule
} from "@/store/modules";
import DoesNotExistScreen from "@/components/misc/DoesNotExistScreen.vue";
import SpeedDial, { ISpeedDialItem } from "@/components/misc/SpeedDial.vue";

@Component({
  components: {
    NumberCard,
    TheLoginBox,
    DoesNotExistScreen,
    JobEditor,
    RepositoryEditor,
    HomeWizard,
    SpeedDial
  }
})
export default class ClientView extends Mixins(LoadingScreen) {
  dialogInstall = false;
  dialogDelete = false;
  dialogSetup = false;
  backupInstalled = false;
  installCurrentlyRunning = false;
  countActiveBackupjobs = 0;
  countRespositories = 0;
  repos: IRepository[] = [];

  mounted() {
    this.refresh();
  }
  get getClientId(): string {
    return routerModule.params.clientId;
  }

  get clientData() {
    return adminModule.clientbyID(this.getClientId);
  }

  async client() {
    const client = await adminModule.getClientInfo(this.getClientId);
    return client;
  }

  get checkClientToken() {
    return adminModule.curClientToken;
  }

  async getNumberOfActiveBackupJobs() {
    const resp = await adminModule.getActiveBackupJobs(this.getClientId);
    if (resp) {
      if (resp.payload) {
        this.countActiveBackupjobs = resp.payload.jobs.length;
        return;
      }
    }
    this.countRespositories = 0;
  }

  async refresh() {
    try {
      if (!this.checkClientToken) {
        await this.getClientUser();
      }
      const resp = await this.checkInstallStatus();
      if (resp) {
        await this.getNumberOfActiveBackupJobs();
        await this.getNumberOfRepositories();
        await this.getRepositories();
      }
    } catch (e) {
      //console.log("error", e);
    }
  }

  async getClientUser() {
    try {
      const resp = await adminModule.getClientUser(this.getClientId);
    } catch (e) {
      //console.log(e);
    }
  }

  async getNumberOfRepositories() {
    const resp = await adminModule.getRepos(this.getClientId);
    if (resp) {
      if (resp.payload) {
        this.countRespositories = resp.payload.repo.length;
        return;
      }
    }
    this.countActiveBackupjobs = 0;
  }

  async getRepositories() {
    const resp = await adminModule.getRepos(this.getClientId);
    if (resp) {
      if (resp.payload) {
        this.repos = resp.payload.repo.map(
          r =>
            ({
              autoUnlockDelay: r.autoUnlock,
              id: r.id,
              location: r.repoLocation,
              name: r.repoName,
              password: r.repoPassword,
              s3AccessKey: r.accessKey,
              s3SecretKey: r.secretAccessKey,
              type: r.repoType
            } as IRepository)
        );
        return;
      }
    }
    this.countActiveBackupjobs = 0;
  }

  async deleteClient() {
    await adminModule.deleteClient(Number(this.getClientId));
    this.$router.replace("/admin");
  }
  async installBackupOnClient(data: ILoginArgs) {
    this.dialogInstall = false;
    //this.installCurrentlyRunning = true;
    await adminModule.installBackupOnClient({
      id: this.getClientId,
      username: data.username,
      password: data.password
    });
  }
  async deleteBackupOnClient(data: ILoginArgs) {
    this.dialogDelete = false;
    await adminModule.deleteBackupOnClient({
      id: this.getClientId,
      username: data.username,
      password: data.password
    });
  }
  async createRepoOnClient(data: IRepository) {
    /* const repodata: IRepositoryOptionalId = {
      archived: false,
      repoType: data.type,
      repoName: data.name,
      autoUnlock: data.autoUnlockDelay,
      repoPassword: data.password,
      repoLocation: data.location,
      accessKey: data.s3AccessKey ? data.s3AccessKey : "",
      secretAccessKey: data.s3SecretKey ? data.s3SecretKey : ""
    }; */
    const repoClient: IRepositoryWithClientId = {
      clientId: this.getClientId,
      clientrepo: data
    };
    adminModule.createRepoOnClient(repoClient);
  }
  async checkInstallStatus() {
    const resp = await adminModule.checkInstallStatus(this.getClientId);

    if (resp) {
      if (resp.payload) {
        this.backupInstalled = resp.payload.installed;
        return resp;
      }
    }
    this.backupInstalled = false;
    return undefined;
  }

  async navigateToNewRepo() {
    const resp = await this.client();
    if (resp) {
      if (resp.payload) {
        const str = "\\\\" + resp.payload.client.ip;
        await this.$router.replace({
          path: `/admin/client/${this.getClientId}/repositories/new`
        });
      }
    }
  }

  async createBackupJobOnClient(data: JobOptionalId) {
    const id = this.getClientId;
    const jobClient: IBackupJobWithClientId = {
      clientjob: data,
      clientId: id
    };
    adminModule.createBackupJobOnClient(jobClient);
  }

  get speedDialItems(): ISpeedDialItem[] {
    return [
      {
        to: `/admin/client/${this.getClientId}/edit`,
        icon: "mdi-pencil",
        name: this.$t("client.edit-client").toString()
      },
      {
        click: this.deleteClient,
        icon: "mdi-delete",
        name: this.$t("client.delete-client").toString()
      },
      {
        click: this.refresh,
        icon: "mdi-refresh",
        name: this.$t("client.refresh-client").toString()
      }
    ];
  }
}
export interface IRepository {
  name: string;
  password: string;
  location: string;
  s3AccessKey: string;
  s3SecretKey: string;
  id: number;
  type: RepoType;
  autoUnlockDelay: number;
}

export interface IBackupJobWithClientId {
  clientjob: JobOptionalId;
  clientId: string;
}
